* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 40vh;
}

.btn {
  padding: 20px;
  color: #777;
  font-weight: 500;
  font-size: 2.5rem;
  border-radius: 5px;
  border: none;
}

.btn-green {
  background: linear-gradient(90deg, rgb(120, 255, 120), rgb(52, 226, 52));
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.527);
  cursor: pointer;
}

.btn-white {
  background: linear-gradient(90deg, rgb(255, 255, 255), rgb(255, 255, 255));
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.527);
  cursor: pointer;
}

.btn:hover {
  transform: translateY(-3px);
}

.btn:active {
  transform: translateY(-1px);
}

.btn:focus {
  outline: none;
}


.spinner {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: white;
  z-index: 9999;
  display: inline-block;
}

.actual-spinner {
  position: absolute;
  top: 50%;
  left: 44%;
  transform: translate(-50%, -50%);
  height: 50px;
  width: 50px;
  border-radius: 50px;
  border: 6px solid grey;
  border-right: 6px solid teal;
  animation: spin 0.7s linear infinite;
}


@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


@media only screen and (max-width: 1000px) {
  .container {
    flex-direction: column;
    padding: 100px;
    margin-top: 0;
  }

  .container button {
    margin: 20px;
  }
}